
audio {
  display: none;
}
.player {
  width: 640px;
  margin: 0 auto;
  border: 1px solid #ccc;
  padding: 15px;
}
.player-title {
  height: 30px;
  line-height: 30px;
  text-align: center;
  margin-bottom: 30px;
}
.player-progress {
  width: 90%;
  height: 5px;
  margin: auto;
  border: 1px solid #ccc;
  background: #e8e8e8;
  border-radius: 2px;
  overflow: hidden;
}
.player-progress span {
  display: block;
  width: 10%;
  height: 100%;
  background: #666;
}
.player-controls {
  margin-top: 50px;
  text-align: center;
}
.player-control {
  display: inline-block;
  width: 50px;
  height: 50px;
  line-height: 50px;
  border: 1px solid #ccc;
  text-align: center;
  margin: 0 5px;
}



